:root {
  --talesoftheoldwest-c-dark: #191813;
  --talesoftheoldwest-c-faint: #c9c7b8;
  --talesoftheoldwest-c-beige: #b5b3a4;
  --talesoftheoldwest-c-tan: #444444;
  --talesoftheoldwest-c-white: #ffffff;
  --talesoftheoldwest-c-black: #000000;
  --talesoftheoldwest-c-groove: #eeede0;
}

.theme-dark {
  --talesoftheoldwest-c-dark: #191813;
  --talesoftheoldwest-c-faint: #c9c7b8;
  --talesoftheoldwest-c-beige: #b5b3a4;
  --talesoftheoldwest-c-tan: #444444;
  --talesoftheoldwest-c-white: #ffffff;
  --talesoftheoldwest-c-black: #000000;
  --talesoftheoldwest-c-groove: #eeede0;
}

.talesoftheoldwest .chat.roll {
  color: var(--black);
  font-family: "fraunces-normal-400";
  font-size: 12px;
  line-height: 16px;
  padding: 5px;
}

.talesoftheoldwest .chat.roll .foreground {
  padding: 5px;
}

.talesoftheoldwest .chat.roll .border {
  border: 1px solid transparent;
  border-color: var(--color-border);
  border-image: var(--chat-roll-border) 20/10px;
}

.talesoftheoldwest .chat.roll strong {
  text-transform: uppercase;
  font-weight: bold;
  color: var(--talesoftheoldwest-c-beige);
  font-size: 14px;
}

.talesoftheoldwest .chat.roll h3 {
  font-family: "fraunces-normal-400";
  font-size: 14px;
  font-weight: bold;
  color: var(--talesoftheoldwest-c-beige);
  margin: 0 0 8px;
  text-align: center;
  border-bottom: 1px solid var(--color-border);
}

.talesoftheoldwest .chat.roll h4 {
  font-family: "fraunces-normal-400";
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0 10px 0;
  text-align: center;
}

.talesoftheoldwest .chat.roll p {
  font-size: 16px;
  font-weight: bold;
  margin: 0 0 4px;
}

.talesoftheoldwest .chat.roll .tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}

/* Tooltip text */
.talesoftheoldwest .chat.roll .tooltip .tooltiptext {
  visibility: hidden;
  width: 10em;
  background-color: var(--black);
  color: var(--white);
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

.talesoftheoldwest .chat.roll .right {
  top: -15%;
  left: 105%;
}

.talesoftheoldwest .chat.roll .tooltip:hover .tooltiptext {
  visibility: visible;
}

.talesoftheoldwest .chat.roll img,
.talesoftheoldwest .chat.roll .portrait {
  border: 0;
  display: inline-block;
  margin: 0 auto 8px;
  height: 40px;
  width: auto;
}

.talesoftheoldwest .dice-push {
  flex: 0 0 22px;
  font-size: 12px;
  text-align: center;
}

.talesoftheoldwest .dice-background {
  flex: 0 0 100%;
  position: relative;
  margin: 0;
  line-height: 24px;
  text-align: center;
  background: rgba(0, 0, 0, 0.1);
  border: 1px solid var(--color-border-light-2);
  border-radius: 3px;
  box-shadow: 0 0 2px #fff inset;
  word-break: break-all;
}

.chat-message {
  font-family: "fraunces-normal-400";
  color: black;
  background-image: url("../assets/background/sheet-background.webp");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: auto; /* Resize the background image to cover the entire container */
  background-origin: content-box;
}

/* Global styles */
.theme-dark .application.sheet.actor.talesoftheoldwest,
.theme-light .application.sheet.actor.talesoftheoldwest,
.theme-dark .application.sheet.item.talesoftheoldwest,
.theme-light .application.sheet.item.talesoftheoldwest {
  background: transparent;
}

body.theme-dark input[type=number],
body.theme-light input[type=number] {
  background-color: white;
  color: black;
}

body.theme-dark input[type=text],
body.theme-light input[type=text] {
  background-color: white;
  color: black;
}

.talesoftheoldwest .rollable:hover, .talesoftheoldwest .rollable:focus {
  color: #000;
  text-shadow: 0 0 10px red;
  cursor: pointer;
}

.theme-dark input[type=checkbox],
.theme-light input[type=checkbox] {
  --checkbox-font-weight: unset;
  --checkbox-background-color: var(--color-dark-6);
  --checkbox-checked-color: var(--color-warm-1);
  --checkbox-checkmark-color: var(--color-dark-1);
}

.theme-dark input[type=checkbox]::before,
.theme-light input[type=checkbox]::before {
  color: white;
}

.talesoftheoldwest.dice-tooltip .roll > img {
  width: 24px;
}

body .application.dialog {
  width: 300px;
  background-image: url("../assets/background/sheet-background.webp");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: 100% 100%;
  border: transparent;
  filter: drop-shadow(8px 12px 8px #353232);
}
body .dialog-form.standard-form .form-footer,
body .dialog-buttons {
  justify-content: center;
}
body .dialog-form.standard-form .form-footer button,
body .dialog-buttons button {
  max-width: max-content;
  min-width: unset;
  width: fit-content;
}
body .application.sheet.actor.talesoftheoldwest .window-content,
body .application.sheet.item.talesoftheoldwest .window-content {
  display: block;
  overflow: auto;
  backdrop-filter: blur(4px);
  scrollbar-color: #d36913 transparent;
  scrollbar-width: thin;
  background-image: url("../assets/background/sheet-background.webp");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: 100% 100%;
  border: transparent;
  filter: drop-shadow(8px 12px 8px #353232);
  font-family: "fraunces-normal-400";
  color: hsl(0, 0%, 0%);
}
body .application input[type=text],
body .application select {
  font-family: "fraunces-normal-400";
  /* font-size: 1.25em; */
  background: white;
  color: black;
  --input-background-color: rgba(0, 0, 0, 0.1);
  --input-border-color: var(--color-dark-6);
  --input-text-color: var(--color-dark-2);
  --input-placeholder-color: var(--color-dark-3);
  --input-focus-outline-color: var(--color-warm-2);
}
body .application input[type=number] {
  width: 44px;
  font-family: "fraunces-normal-400";
  font-size: 1.25em;
  background: white;
  color: black;
  --input-background-color: rgba(0, 0, 0, 0.1);
  --input-border-color: var(--color-dark-6);
  --input-text-color: var(--color-dark-2);
  --input-placeholder-color: var(--color-dark-3);
  --input-focus-outline-color: var(--color-warm-2);
}

.dialog-content.standard-form .form-group > label,
.dialog-content.standard-form .form-group > span.label,
.dialog-form.standard-form {
  color: black;
}

.talesoftheoldwest.application .window-header,
.window-app .window-header {
  background-image: url("../assets/background/header-plank.webp");
}

.talesoftheoldwest.application .window-resize-handle {
  position: absolute;
  inset: auto -2px -3px auto;
  width: 24px;
  height: 24px;
  cursor: nwse-resize;
  background: transparent url(../ui/DsN/trouble-face.webp) no-repeat center/contain;
}

.talesoftheoldwest .div-border {
  border: 3px solid black;
}

.talesoftheoldwest.application .hr-style {
  background-image: linear-gradient(90deg, transparent 0%, #000000 50%, transparent 100%);
  margin: 5px;
}

.talesoftheoldwest .sheet-tabs {
  flex: 0;
  font-size: large;
  font-family: "fraunces-normal-400";
  font-weight: bold;
  background-image: url("../assets/background/header-plank.webp");
  width: 97%;
  justify-self: anchor-center;
}

.talesoftheoldwest .tab.description {
  flex: 1;
}
.talesoftheoldwest .tab.description .editor {
  height: 100%;
}

.talesoftheoldwest .chat-buttons {
  font-size: 25px;
  font-weight: bold;
  background: url("../assets/background/button-planks.webp") no-repeat center;
  background-size: 241px;
  width: 241px;
  height: 41px;
  padding: 0px 13px 0px 15px;
  justify-content: center;
  align-content: center;
  justify-self: center;
  filter: drop-shadow(8px 11px 7px black);
  text-shadow: 2px 2px 2px white;
  margin-bottom: 10px;
}

.talesoftheoldwest .pushed-button {
  font-size: 25px;
  font-weight: bolder;
  font-style: italic;
  background: url("../assets/background/button-planks-pushed.webp") no-repeat center;
  background-size: 241px;
  width: 241px;
  height: 41px;
  padding: 0px 13px 0px 15px;
  justify-content: center;
  align-content: center;
  justify-self: center;
  margin-bottom: 10px;
  filter: drop-shadow(8px 11px 7px black);
  text-shadow: 2px 2px 2px white;
}

.app.window-app.sheet.journal-sheet.journal-entry .journal-entry-content {
  background-image: url("../assets/background/journal-background.webp");
  background-size: cover;
  color: #000;
}

.app.window-app.sheet.journal-sheet.journal-entry .journal-header .title {
