/* Import settings */
.talesoftheoldwest .container {
  width: 100%;
}

.talesoftheoldwest .listHeading {
  text-align: center;
  vertical-align: middle;
  padding: 10px 0px;
  border-bottom: 1px solid black;
  margin-bottom: 10px;
}

.talesoftheoldwest .twogridcol {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(160px, 50%));
}

.talesoftheoldwest .twogridcol input[type=checkbox] {
  vertical-align: middle;
}

.talesoftheoldwest .actors.content {
  background: lightgray;
}

.talesoftheoldwest .actors.content.toggle-display {
  display: none;
}

.talesoftheoldwest .items.content {
  background: lightgray;
}

.talesoftheoldwest .items.content.toggle-display {
  display: none;
}

.talesoftheoldwest .journal.content {
  background: lightgray;
}

.talesoftheoldwest .journal.content.toggle-display {
  display: none;
}

.talesoftheoldwest .scenes.content {
  background: lightgray;
}

.talesoftheoldwest .scenes.content.toggle-display {
  display: none;
}

.talesoftheoldwest table {
  background: none;
}

.talesoftheoldwest .tables.content {
  background: lightgray;
}

.talesoftheoldwest .tables.content.toggle-display {
  display: none;
}

.talesoftheoldwest .macros.content {
  background: lightgray;
}

.talesoftheoldwest .macros.content.toggle-display {
  display: none;
}

.talesoftheoldwest .title {
  cursor: pointer;
}

/* Footer */
.talesoftheoldwest .footer {
  position: sticky;
  bottom: 0;
  left: 0;
  width: inherit;
  height: auto;
  background-color: #d0daee;
}

.talesoftheoldwest .imagecontainer {
  position: relative;
  text-align: center;
  color: white;
}

/* Bottom left text */
.talesoftheoldwest .image-text-box {
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: 30px;
}

.talesoftheoldwest .inline-link2 .content-link,
a.content-link {
  background-color: transparent;
  color: hsl(6, 56%, 30%);
  border: 0;
  font-family: "fraunces-normal-400";
  font-style: italic;
  text-transform: capitalize;
  text-wrap-mode: wrap;
  word-break: keep-all;
}

.talesoftheoldwest a.content-link i,
.talesoftheoldwest .fa-file-lines:before {
  content: unset;
}

.talesoftheoldwest .totowtable {
  height: 36px;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  border: 0;
}

.talesoftheoldwest .totowtable tr:nth-child(even) {
  background-color: transparent;
}

.talesoftheoldwest .tdstyle {
  width: 40%;
  height: 18px;
  text-align: left;
}

.talesoftheoldwest .tdstyleR {
  width: 40%;
  height: 18px;
  text-align: Right;
}

.talesoftheoldwest .tdstyleC {
  width: 100%;
  height: 18px;
  text-align: center;
}

.talesoftheoldwest .tdstyle3D {
  width: 30%;
  height: 18px;
  text-align: center;
}

.talesoftheoldwest .tdstyle2D {
  width: 50%;
  height: 18px;
  text-align: center;
}

